@import url('https://fonts.googleapis.com/css?family=Neucha&subset=cyrillic');

.app-lib {
    font: 18px 'Comic Sans MS', serif;
    margin: 0 0.7em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
}

.fieldset-container {
    width: 100%;
    height: 93%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.input {
    font: 1em 'Comic Sans MS', serif;
    max-width: 8.5em;
    border: 2px solid black;
    display: block;
}

.app-lib label {
    display: block;
}

.button {
    font: 0.9em 'Comic Sans MS', serif;
    outline: none;
    border: 2px solid black;
    border-radius: 0.2em;
    -webkit-box-shadow: 0.15em 0.15em 0.2em black;
            box-shadow: 0.15em 0.15em 0.2em black;
    background-color: white;
}

.button:hover {
    cursor: pointer;
    background-color: #DDDDDD;
}

.button:active {
    -webkit-box-shadow: 0.1em 0.1em 0.1em black;
            box-shadow: 0.1em 0.1em 0.1em black;
    -webkit-transform: translateY(0.05em);
        -ms-transform: translateY(0.05em);
            transform: translateY(0.05em);
}

.fieldset {
    border: 2px solid black;
    margin: 1em;
}

@media screen and (max-width: 620px), screen and (max-device-width: 620px) {
    .app-lib {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }

    .fieldset {
        margin: 0.5em 1em;
    }
}



