.filter-sort {
    width: 100%;
    min-height: 8.2em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.filter-sort .fieldset-container {
    height: 65%;
}

.filter {
    min-width: 13em;
}

.sort {
    min-width: 13em;
}

.search_button {
    float: right;
}

@media screen and (max-width: 620px), screen and (max-device-width: 620px) {
    .filter-sort {
        -ms-flex-pack: center;
            justify-content: center;
    }
}

@media screen and (-ms-high-contrast: active) and (max-width: 900px), screen and (-ms-high-contrast: none) and (max-width: 900px) {
    .filter-sort {
        max-width: 30em;
    }
}


